import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import { useEffect } from 'react';
import LocomotiveScroll from 'locomotive-scroll';
import 'locomotive-scroll/src/locomotive-scroll.scss';

gsap.registerPlugin(ScrollTrigger);

export default function useLocoScroll(start) {
	useEffect(() => {
		if (!start) return;
		let locoScroll = null;
		let scrollEnd = true;

		const scrollEl = document.querySelector('#main-section__body');

		if (!scrollEl) return; //         

		locoScroll = new LocomotiveScroll({
			el: scrollEl,
			smooth: true,
			multiplier: 1,
			class: 'is-reveal',
		});

		const ReFrEsH = () => {
			if (scrollEnd) ScrollTrigger.refresh();
		};

		//       
		const handleScrollEnd = () => {
			const scrollPosition = locoScroll.scroll.instance.scroll.y;
			const scrollLimit = locoScroll.scroll.instance.limit;

			//console.log(scrollPosition, scrollLimit.y - 200);
			//console.log(locoScroll.scroll.instance.limit);
			if (scrollLimit.y === 0) {
				return;
			}
			if (scrollPosition >= scrollLimit.y) {
				ReFrEsH(scrollEnd);
				ScrollTrigger.update();
				//console.log('  ');
				scrollEnd = !scrollEnd;
				locoScroll.stop();
				setTimeout(1000);
				locoScroll.start();
			} else {
				ScrollTrigger.update();
				//console.log('');
				//console.log(locoScroll.scroll.instance);
			}
		};

		//   
		locoScroll.on('scroll', handleScrollEnd);

		ScrollTrigger.scrollerProxy(scrollEl, {
			scrollTop(value) {
				if (locoScroll) {
					return arguments.length
						? locoScroll.scrollTo(value, 0, 0)
						: locoScroll.scroll.instance.scroll.y;
				}
				return null;
			},
			scrollLeft(value) {
				if (locoScroll) {
					return arguments.length
						? locoScroll.scrollTo(value, 0, 0)
						: locoScroll.scroll.instance.scroll.x;
				}
				return null;
			},
		});

		const lsUpdate = () => {
			if (locoScroll) {
				locoScroll.update();
			}
		};

		ScrollTrigger.addEventListener('refresh', lsUpdate);
		ScrollTrigger.refresh();

		return () => {
			if (locoScroll) {
				ScrollTrigger.removeEventListener('refresh', lsUpdate);
				locoScroll.destroy();
				locoScroll = null;
				console.log('Kill', locoScroll);
			}
		};
	}, [start]);
}
